<!--  -->
<template>
  <div class="zui" >
   <Zi  goback="true" data="<" :con=obj.name bool1="false" main="切换城市">
      <h1 @click="$router.go(-1)" slot="left">
        <
      </h1>
       <!-- <router-link to="/" tag="h1" slot='name' class="c"><</router-link>
       <h1 slot='name3' class="b">
         {{obj.name}}
       </h1>
       
               <router-link to="/" class="a" tag="h1" slot='name2'>切换城市</router-link> -->
   </Zi>
        <div class="header">
          <input @keydown="submita()" v-model="str"  type="text" placeholder="输入学校、商务楼、地址">
          <button @click="submit()">提交</button>
        </div>
      
        <ul v-show="this.bool">
         <span class="search">搜索历史</span>
          <!-- <li class="xuan"> -->
           <router-link tag="li"  v-for="item of arr" style="padding-left:1rem" :key="item.id" :to="{name:'Banner',params:{name:item.name}}" >
            <p>{{item.name}}</p>
            <p class="di">{{item.address}}</p>
            </router-link>

            <li v-show="this.bool2" style="padding-left:2rem;line-height:1rem" @click="del">清空所有</li>
          <!-- </li> -->
        </ul>
        <ul>
          <li  class="xuan" @click="submit2(item)"  style="padding-left:1rem" v-for="(item,index) of list" :key="index">
               <router-link @click="submit2(item)" :to="{name:'Banner',params:{name:item.name}}" tag="div">
            <p>{{item.name}}</p>
           <p  class="di"> {{item.address}}</p>
               </router-link>
        
          </li>
        </ul>
  </div> 
</template>

<script>
import Zi from "../../../components/header"
export default {
  data () {
    return {
      obj:{},
      str:"",
      list:[],
      arr:[],
      bool:true,
      bool2:false
    };
  },

  components: {Zi},

  computed: {},

  mounted(){
  if(this.arr.length>0){
    this.bool2=true
  }
    console.log(this.arr)
    var a= this.$route.params.id
    var that=this
    this.http.get("http://elm.cangdu.org/v1/cities/"+a).then((d)=>{
      that.obj=d.data
    })
     
  },

  created(){
      if(localStorage.getItem("searchList")){
      this.arr=JSON.parse(localStorage.getItem("searchList"))
    }
  },

  methods: {
    submit(){
      var that=this
       this.http.get('http://elm.cangdu.org/v1/pois?type=search&city_id='+this.$route.params.id+'&keyword='+this.str).then((d)=>{
        that.list=d.data
      })
     this.bool=false ;
    },
  submit2(item){
     localStorage.setItem("nameList",JSON.stringify(item))
   var bool=true;
  this.arr.map((ite,index)=>{
    if(item.name==ite.name){
        bool=false 
    }
  })
  if(bool==true){
  this.arr.unshift(item)
  }
   localStorage.setItem("searchList",JSON.stringify(this.arr))
  

    },
    del(){
    
      this.arr=[]
      localStorage.removeItem("searchList",null)
          if(this.arr.length>0){
              this.bool2=true
            }else{
              this.bool2=false
            }
    },
    submita(){
      var key = window.event.keyCode;
     if(key==13){
        var that=this
       this.http.get('http://elm.cangdu.org/v1/pois?type=search&city_id='+this.$route.params.id+'&keyword='+this.str).then((d)=>{
        that.list=d.data
      })
     this.bool=false ;
     }
  }
}
}

</script>
<style scoped lang="scss">
.b{
  text-align: center
}
h1{
  &.a{
    float: right;
    text-align: right
  }
}
.zui{
  height:8rem;
  background: #f5f5f5;
}
.header{
  margin-top: 1rem!important;
  display: flex;
  flex-direction: column;
  background: white;
  margin-top: 0.3rem;
  height: 2.4rem;
  padding-top:0.2rem;
  font-size: 0.3rem;
  input,button{
    width: 80%;
    height: 0.8rem;
    display: block;
    font-size: 0.26rem;
    padding-left:0.3rem;
    margin-top: 0.2rem;
    border: 1px solid ;
    margin: 0 auto
  }
  button{
    width: 84%;
    border-radius: 0.04rem;
    background: #3190e8;
    color: white;
    margin-top: 0.2rem;
  }
}

.search{
  padding-left: 0.2rem;
  display: block;
  border-bottom: 1px solid #ccc;
}
li{
  div{
    height: 1rem;
    line-height: 1rem
  }
}
li{
  background: white;
  width: 100%;
  height: 1rem;
  border-bottom: 1px solid #ccc;
  p{
    line-height: 0.5rem
  }
 .di{
   line-height: 0.5rem;
   overflow: hidden;
   font-size: 0.18rem;
   color: #ccc

 }
}
 .xuan{
    line-height: 0.5rem
  }
  h1{font-size: 0.3rem;}
  .c{
    width: 30%;
    padding-left: 3%
  }
</style>


